<template>
  <div>
    <van-nav-bar title="分类" />
    <van-sidebar v-model="activeKey">
      <van-sidebar-item
        :title="item.catename"
        v-for="(item,index) in firstCate"
        :key="item.id"
        @click="changeCate(index)"
      />
    </van-sidebar>
    <div class="cate">
      <van-grid :border="false" :column-num="3">
        <van-grid-item v-for="item in secondCate" :key="item.id" :to="`/goodsList?fid=${item.id}`" >
          <van-image :src="$preImg+item.img" />
          <p>{{item.catename}}</p>
        </van-grid-item>
      </van-grid>
    </div>
  </div>
</template>

<script>
import { getCateTree } from "../utils/req";
export default {
  data() {
    return {
      activeKey: 0,
      firstCate: [],
      secondCate: [],
    };
  },
  methods: {
    reqCate() {
      getCateTree().then((res) => {
        this.firstCate = res.data.list;
        this.changeCate(0);
      });
    },
    changeCate(index) {
      this.secondCate = this.firstCate[index].children;
    },
  },
  mounted() {
    this.reqCate();
  },
};
</script>

<style>
.cate{
 position: absolute;
  top:55px;
  right:0;
  margin-left:80px;
}
</style>


